<template>
  <div class="customer-container">
    <el-card>
      <div slot="header" class="clearfix">
        <span>客户管理</span>
        <el-button type="primary" size="small" @click="handleAdd" style="float: right">新增客户</el-button>
      </div>

      <!-- 查询条件 -->
      <el-form :inline="true" :model="searchForm" class="search-form">
        <el-form-item label="客户编码">
          <el-input v-model="searchForm.customerCode" placeholder="请输入客户编码"></el-input>
        </el-form-item>
        <el-form-item label="客户名称">
          <el-input v-model="searchForm.customerName" placeholder="请输入客户名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleSearch">查询</el-button>
          <el-button @click="handleReset">重置</el-button>
        </el-form-item>
      </el-form>
      <!-- 表格数据 -->
      <el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column prop="customerCode" label="客户编码"></el-table-column>
        <el-table-column prop="customerName" label="客户名称"></el-table-column>

        <el-table-column prop="status" label="状态">
          <template slot-scope="scope">
            <el-tag :type="scope.row.status === 1 ? 'success' : 'danger'">
              {{ scope.row.status === 1 ? '启用' : '禁用' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="180" fixed="right">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="handleEdit(scope.row)">编辑</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <div class="pagination-container">
        <el-pagination
          background
          :current-page="currentPage"
          :page-size="pageSize"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </el-card>
    <!-- 客户表单对话框 -->
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="60%">
      <el-form :model="form" :rules="rules" ref="customerForm" label-width="100px">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="客户编码" prop="customerCode">
              <el-input v-model="form.customerCode" placeholder="自动生成" readonly></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="客户名称" prop="customerName">
              <el-input v-model="form.customerName" placeholder="请输入客户名称"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="状态" prop="status">
              <el-switch v-model="form.status" :active-value="1" :inactive-value="0"></el-switch>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import {customerAPI} from "@/api/api";

export default {
  name: 'Customer',
  data() {
    return {
      // 查询条件
      searchForm: {
        customerCode: '',
        customerName: '',
        status: ''
      },
      // 表格数据
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
      // 选中的行
      selectedRows: [],
      // 对话框
      dialogVisible: false,
      dialogTitle: '',
      // 表单数据
      form: {
        id: null,
        customerCode: '',
        customerName: '',
        status: 1
      },
      // 表单验证规则
      rules: {
        customerCode: [
          { required: true, message: '客户编码不能为空', trigger: 'blur' }
        ],
        customerName: [
          { required: true, message: '客户名称不能为空', trigger: 'blur' }
        ]
      },
      // 当前用户信息
      currentUser: {
        id: 1,
        companyId: 1
      }
    }
  },
  mounted() {
    this.loadData()
  },
  methods: {
    // 加载数据
    loadData() {
      const params = {
        pageNum: this.currentPage,
        pageSize: this.pageSize,
        companyId: this.currentUser.companyId,
        ...this.searchForm
      }

      customerAPI.page(params).then(res => {
        if (res.code === 200) {
          this.tableData = res.data.records
          this.total = res.data.total
        } else {
          this.$message.error(res.message)
        }
      }).catch(err => {
        this.$message.error('加载数据失败')
      })
    },

    // 查询
    handleSearch() {
      this.currentPage = 1
      this.loadData()
    },

    // 重置
    handleReset() {
      this.searchForm = {
        customerCode: '',
        customerName: '',
        status: ''
      }
      this.loadData()
    },

    // 分页大小改变
    handleSizeChange(size) {
      this.pageSize = size
      this.loadData()
    },

    // 当前页改变
    handleCurrentChange(current) {
      this.currentPage = current
      this.loadData()
    },

    // 选择行变化
    handleSelectionChange(rows) {
      this.selectedRows = rows
    },

    // 新增
    handleAdd() {
      this.dialogTitle = '新增客户'
      customerAPI.generateCode({companyId: 1}).then(res => {
        if (res.code === 200) {
          this.form = {
            id: null,
            customerCode: res.data,
            customerName: '',
            status: 1,
          }
          this.dialogVisible = true
        } else {
          this.$message.error(res.message)
        }
      })
    },

    // 编辑
    handleEdit(row) {
      this.dialogTitle = '编辑客户'
      this.form = {...row}
      this.dialogVisible = true
    },

    // 删除
    handleDelete(id) {
      this.$confirm('确定要删除该客户吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        customerAPI.delete(id).then(res => {
          if (res.code === 200) {
            this.$message.success('删除成功')
            this.loadData()
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('删除失败')
        })
      })
    },

    // 批量删除
    handleBatchDelete() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要删除选中的 ${ids.length} 个客户吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        customerAPI.deleteBatch(ids).then(res => {
          if (res.code === 200) {
            this.$message.success('批量删除成功')
            this.loadData()
            this.selectedRows = []
          } else {
            this.$message.error(res.message)
          }
        }).catch(err => {
          this.$message.error('批量删除失败')
        })
      })
    },

    // 批量启用
    handleBatchEnable() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要启用选中的 ${ids.length} 个客户吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 批量启用逻辑
        // 这里简化处理，实际应该调用批量启用接口
        ids.forEach(id => {
          const params = {
            id: id,
            status: 1,
            updateBy: this.currentUser.id
          }
          customerAPI.update(params).then(res => {
            if (res.code !== 200) {
              this.$message.error(res.message)
            }
          })
        })
        this.$message.success('批量启用成功')
        this.loadData()
        this.selectedRows = []
      })
    },

    // 批量禁用
    handleBatchDisable() {
      const ids = this.selectedRows.map(row => row.id)
      this.$confirm(`确定要禁用选中的 ${ids.length} 个客户吗？`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 批量禁用逻辑
        // 这里简化处理，实际应该调用批量禁用接口
        ids.forEach(id => {
          const params = {
            id: id,
            status: 0,
            updateBy: this.currentUser.id
          }
          customerAPI.update(params).then(res => {
            if (res.code !== 200) {
              this.$message.error(res.message)
            }
          })
        })
        this.$message.success('批量禁用成功')
        this.loadData()
        this.selectedRows = []
      })
    },

    // 取消对话框
    handleCancel() {
      this.dialogVisible = false
      this.$refs.customerForm.resetFields()
    },

    // 提交表单
    handleSubmit() {
      this.$refs.customerForm.validate((valid) => {
        if (valid) {
          if (this.form.id) {
            // 编辑
            customerAPI.update(this.form).then(res => {
              if (res.code === 200) {
                this.$message.success('更新成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('更新失败')
            })
          } else {
            // 新增
            customerAPI.add(this.form).then(res => {
              if (res.code === 200) {
                this.$message.success('新增成功')
                this.dialogVisible = false
                this.loadData()
              } else {
                this.$message.error(res.message)
              }
            }).catch(err => {
              this.$message.error('新增失败')
            })
          }
        }
      })
    },

    // 检查客户名称是否重复
    checkNameDuplicate() {
      if (!this.form.customerName) return

      customerAPI.checkNameDuplicate(this.form.customerName, this.currentUser.companyId, this.form.id).then(res => {
        if (res.code === 200 && res.data) {
          this.$message.warning('客户名称已存在')
          this.$refs.customerForm.validateField('customerName')
        }
      })
    }
  }
}
</script>

<style scoped>
.customer-container {
  padding: 20px;
}

.search-form {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  text-align: right;
}

.batch-operation {
  margin-top: 10px;
}
</style>
